}

/* Result Component */
.result {
  padding: 1.5rem 2.5rem;
}

/* Quiz Component */
.quiz {
  padding: 2rem;
}

.answerOptions {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 18px;
}

/* QuestionCount Component */
.questionCount {
    text-align: center;
    margin-bottom: 1.5rem;
}

/* Question Component */
.question {
    margin: 0;
    text-align: center;
    margin-bottom: 2rem;
}

/* AnswerOption Component */
.answerOption {
    border-top: 1px solid #eee;
}

.answerOption:first-child {
  border-top: none;
}

.answerOption:hover {
  background-color: #eafaf2;
}

.radioCustomButton {
  position: absolute;
  width: auto;
  opacity: 0;
}

.radioCustomButton,
.radioCustomLabel {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}

.radioCustomLabel {
  position: relative;
  width: 230%;
  margin-left: 5%;
  padding: 1.5rem 2.5rem 1.5rem 7rem;
  line-height: 1.5;
}

.radioCustomButton ~ .radioCustomLabel:before {
  position: absolute;
  top: 28%;
  left: 2%;
  width: 2%;
  height: 40%;
  content: '';
  display: inline-block;
  vertical-align: middle;
  background: #fff;
  border: 1px solid #333;
  border-radius: 50%;
  transition: all 0.3s;
}

.radioCustomButton:checked ~ .radioCustomLabel:before {
  content: '';
  background: #8bc53f url(/plant-app/static/media/icon-check.12ef56db.svg) no-repeat;
  background-size: 100%;
  border-color: #8bc53f;
}

/* Animation */
.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 0.5s ease-in-out 0.3s;
}

.fade-leave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 1;
}

.fade-leave.fade-leave-active {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.fade-appear {
  opacity: 0;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

