}
 /* Customize the label (the container) */
.step_button {
  display: block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.step_button input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.step_button:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.step_button input:checked ~ .checkmark {
  background-color: #72B01D;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.step_button:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.step_button input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.step_button .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
} 

.step_completed {
    margin-top: 15px;
}

.step_completed input {
  margin-right: 5px;
}

#profile {
  margin: 3rem 3rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.vertical-frame {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.profile-content {
  flex: 0 0 70%;
}

.profile-title {
  margin-bottom: 1.5rem;
}

.profile-sub-content {
  margin-top: 1rem;
  margin-bottom: 2rem;
}

.sidebar {
  flex: 0 0 25%;
}

.sidebar-container {
  background-color: #ffffff;
  padding: 2rem;
  box-shadow: 0 4px 5px #ccc;
}

.sidebar .vertical-frame:first-child {
  padding-bottom: 1.5rem;
  align-items: center;
}

.profile-photo {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  
  object-fit: cover;
  margin-left: 10px;
  margin-right: 20px;
}

.profile-username {
  line-height: 40px;
  font-size: 16pt;
  font-weight: bold;
}

.badge-icon {
  width: 30px;
  height: 30px;
  object-fit: cover;
  margin-right: 10px;
}

.badge-icon-gray {
  width: 30px;
  height: 30px;
  object-fit: cover;
  margin-right: 10px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}


.horizontal-divider {
  padding-top: 10pt;
  margin-bottom: 10pt;
  border-bottom: 1px solid black;
  border-bottom-color: #C0C0C0;
  width: 100%;
}

.vertical-divider {
  padding-right: 2vw;
  border-left:1px solid black;
  border-left-color: #C0C0C0;
}

.ul-no-bullets {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.album {
  margin-top: 10px;
}

/* For Care Reminder*/
.list-title {
  background-color: #C0C0C0;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 20px;
  font-weight: bold;
  font-size: 14pt;
}

.list-item {
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 20px;
  font-size: 13pt;
  border-bottom:1px solid black;
  border-bottom-color: #C0C0C0;
}

.content-text {
  font-size: 13pt;
  margin-bottom: 15px;
}

.setting-direction {
  font-size: 13pt;
  text-decoration: underline;
  font-weight: bold;
}

.direction-content {
  font-size: 13pt;
  padding-left: 30px;
}

.direction-content-end {
  font-size: 13pt;
  padding-left: 30px;
  margin-bottom: 15px;
}

.download_button {
  text-decoration: none;
  background-color: #121b21;
  color: #ffffff;
  padding: 10px 20px;
  margin-top: 20px;;
  border-radius: 30px;
}

.round-all.event-item{
  background-color: #3F7D20 !important;
}

.scheduler tbody {
  background-color: #ffffff;
}

.scheduler .header3-text {
  background-color: #ffffff !important;
  color: #121b21 !important;
}

/* For MyProjects */
.new-project-icon {
  background-color: #3F7D20 !important;
}

.grid-layout {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 250px;
  grid-gap: 10px;
  margin: auto;
}

.project-cover-image {
  display: flex;
  width: 200px;
  height: 200px;
  object-fit: cover;
  opacity: 60%;
  -webkit-filter: grayscale(80%);
          filter: grayscale(80%);
}

.new-project-nav {
  text-decoration: none;
}

.new-project-icon {
  font-size: 28pt;
  font-weight: bold;
}

/* For Badges */
.hover-color {
  width: 200px;
  padding: 6px 10px;
  border-color: darkgreen;
  border-style: solid;
  border-width: 1px;
  border-radius: 10px;
  background-color: #eafaf2;
  background-position: center center;
}

.hover-gray {
  width: 200px;
  padding: 6px 10px;
  border-color: darkgreen;
  border-style: solid;
  border-width: 1px;
  border-radius: 10px;
  background-color: #eafaf2;
  background-position: center center;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.badge-image {
  display: inline-block;
  width: 60px;
  height: 60px;
  margin-left: 10px;
  margin-right: 40px;
  margin-bottom: 20px;
  border: none;
  background-position: center center;
  object-fit: cover;
}


.badge-image-color {
  display: inline-block;
  width: 60px;
  height: 60px;
  margin-left: 10px;
  margin-right: 40px;
  margin-bottom: 20px;
  border: none;
  background-position: center center;
  object-fit: cover;
}


.badge-image-gray {
  display: inline-block;
  width: 60px;
  height: 60px;
  margin-left: 10px;
  margin-right: 40px;
  margin-bottom: 20px;
  border: none;
  background-position: center center;
  object-fit: cover;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

/* Centered text */
.text-over-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

